import { data } from "@/data/data";
import Baseline from "./parts/baseline";
import styles from "./timeline.module.scss";

const Timeline = () => {
  const currentYear = new Date().getFullYear();
  const start = data[0].start;
  const zoom = 3;
  return (
    <>
      <h1>title</h1>
      {data.map((t) => {
        return (
          <div key={t.id} className={styles.line}>
            <div className={styles.title}>{t.title}</div>
            <div
              className={styles.element}
              style={{
                width: `${
                  ((t.end ? t.end.year : currentYear) - t.start.year) * zoom
                }px`,
                marginLeft: `${t.start.year * zoom}px`,
              }}
            ></div>
          </div>
        );
      })}
      <Baseline start={start.year} era={start.era} zoom={zoom} />
    </>
  );
};

export default Timeline;
