import { Era } from "@/types/timelineEvents";
import styles from "./baseline.module.scss";
import { getIntermediaryYears } from "@/utils";

interface PBaseline {
  start: number;
  era: Era;
  zoom: number;
}

const Baseline = ({ start, era, zoom }: PBaseline) => {
  const currentYear = new Date().getFullYear();
  const intermediaryYear = 50;
  const years = getIntermediaryYearsEC(currentYear, intermediaryYear);
  return (
    <div className={styles.baseline}>
      {years.map((y, index) => (
        <div
          key={index}
          className={styles.year}
          style={{ marginRight: `${intermediaryYear * zoom}px` }}
        >
          {y}
        </div>
      ))}
    </div>
  );
};

export default Baseline;
